import modelExtend from 'dva-model-extend'
import { query } from '../../services/song'
import { pageModel } from '../../models/common'
import queryString from 'query-string'
interface Ipagination {
  showSizeChanger: boolean,
  showQuickJumper: boolean,
  showTotal: string,
  current: number,
  total: number,
  pageSize: number
}
interface Idetail {
  list: any[],
  pagination: Ipagination
}
interface Istate {
  currentItem: any,
  modalVisible: boolean,
  modelTableData: Idetail
}
export default modelExtend(pageModel, {
  namespace: 'songlists',
  state: {
    currentItem: {},
    modalVisible: false,
    modelTableData: {
      list: [],
      pagination: {
        showSizeChanger: true,
        showQuickJumper: true,
        showTotal: (total: string) => `共 ${total} 条数据`,
        current: 1,
        total: 0,
        pageSize: 10,
      },
    },
  },
  subscriptions: {
    setup ({ dispatch, history }: any) {
      history.listen((location: any) => {
        if (location.pathname === '/song/songlists') {
          dispatch({
            type: 'query',
            payload: {
              // status: 2,
              ...queryString.parse(location.search),
            },
          })
        }
      })
    },
  },
  effects: {
    * query ({ payload }: any, { call, put }: any) {
      const data = yield call(query, payload)
      if (data.success) {
        yield put({
          type: 'querySuccess',
          payload: {
            list: data.data,
            pagination: {
              current: Number(payload.page) || 1,
              pageSize: Number(payload.pageSize) || 10,
              total: data.total,
            },
          },
        })
      } else {
        throw data
      }
    },
    * modeltablequery ({ payload = {} }: any, { call, put }: any) {
      const data = yield call(query, payload)
      if (data.success) {
        yield put({
          type: 'updateState',
          payload: {
            modelTableData: {
              list: data.data,
              pagination: {
                current: Number(payload.page) || 1,
                pageSize: Number(payload.pageSize) || 10,
                total: data.total,
              },
            },
          },
        })
      }
    },
  },
  reducers: {
    updateState (state: Istate, { payload }: any) {
      return {
        ...state,
        ...payload,
      }
    },
  },
})



