import { getLocalStorage } from '../utils/utils'
import config from '../utils/config'
import request from '../utils/request'

const { api } = config
const {
  songlists, songArea, updateAreaTitle, addArea, delArea,
} = api

export function query (params: any) {
  return request({
    url: songlists,
    method: 'get',
    data: params,
  })
}

export function queryAreaList (params: any) {
  return request({
    url: songArea,
    method: 'get',
    data: params,
  })
}
export function changeAreaTitle (params: any) {
  const SystemAdmin = getLocalStorage('SystemAdmin')
  const url = updateAreaTitle.replace(':uid', SystemAdmin.id)
  return request({
    url: url.replace(':id', params.id),
    method: 'put',
    data: {
      title: params.title,
      sort: params.sort,
    },
  })
}
export function deleteArea (params: any) {
  return request({
    url: delArea.replace(':id', params.id),
    method: 'delete',
  })
}
export function queryAddArea (params: any) {
  const SystemAdmin = getLocalStorage('SystemAdmin')
  // console.log(SystemAdmin)
  return request({
    url: addArea.replace(':uid', SystemAdmin.id),
    method: 'post',
    data: params,
  })
}



